<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> 3 товара </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <div class="loader" v-if="cartLoading">
            <span class="loader__text">Загрузка товаров...</span>
            <img src="img/1495.gif" alt="Лоадер" />
          </div>
          <div class="loader__error" v-if="cartLoadingFailed">
            Произошла ошибка при загрузке товаров
          </div>
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе</p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link
            tag="button"
            :to="{ name: 'order' }"
            class="cart__button button button--primery"
            type="submit"
            v-if="productsCount"
          >
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from "vuex";
import CartItem from "@/components/CartItem";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      cartLoading: false,
      cartLoadingFailed: false,
    };
  },
  filters: { numberFormat },
  components: { CartItem },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
    productsCount() {
      return this.$store.state.cartProductsData.length;
    },
  },
  methods: {
    ...mapActions(["loadCart"]),
    loadProductsInCart() {
      if (!this.$store.state.cartProductsData.length) {
        this.cartLoading = true;
      }

      this.cartLoadingFailed = false;

      this.loadCart()
        .then(() => {
          this.cartLoading = false;
        })
        .catch(() => {
          this.cartLoadingFailed = true;
        });
    },
  },
  created() {
    this.loadProductsInCart();
  },
};
</script>
